import { instance, API_URL } from "./instance";

export const getPhotosEndPoint = async (
  roverName: string,
  earth_date = "",
  camera = "all",
  page = 1
) => {
  return instance.get<PhotosApiData>(`${API_URL}/rovers/${roverName}/photos`);
};
// , {
//   params: {
//     earth_date,
//     camera,
//     page
//   }
// }

export interface PhotosApiData {
  photos: Photos[];
}

export interface Photos {
  id: number;
  sol: number;
  camera: Camera;
  img_src: string;
  earth_date: string;
  rover: {
    [index: number]: Rover;
  }
}

export interface Camera {
  id: number;
  name: string;
  rover_id: number;
  full_name: string;
}

export interface Rover {
  id: number;
  name: string;
  landing_date: string;
  launch_date: string;
  status: string;
}
